import { calculate } from "@helpers/calculatorHelpers"

function add(x, y) {
  return parseFloat((+x + +y).toFixed(3))
}
function sub(x, y) {
  return parseFloat((+x - +y).toFixed(3))
}
function mul(x, y) {
  return parseFloat((+x * +y).toFixed(3))
}
function div(x, y) {
  if (y === "0") {
    throw Error("Error: Division by zero")
  } else {
    return parseFloat((+x / +y).toFixed(3))
  }
}

function mod(x, y) {
  return parseFloat((+x % +y).toFixed(3))
}

const Command = function (execute, undo, value) {
  this.execute = execute
  this.undo = undo
  this.value = value
}

const AddCommand = function (value) {
  return new Command(add, sub, value)
}

const SubCommand = function (value) {
  return new Command(sub, add, value)
}

const MulCommand = function (value) {
  return new Command(mul, div, value)
}

const DivCommand = function (value) {
  return new Command(div, mul, value)
}

const ModCommand = function (value) {
  return new Command(mod, div, value)
}

const Calculator = function () {
  let current = 0
  const commands = []

  function action(command) {
    let name = ""
    switch (command.execute.name) {
      case "add":
        name = `${current} + ${command.value} `
        break
      case "sub":
        name = `${current} - ${command.value} `
        break
      case "mul":
        name = `${current} * ${command.value} `
        break
      case "div":
        name = `${current} / ${command.value} `
        break
      case "mod":
        name = `${current} % ${command.value} `
        break
      default:
        break
    }
    return name
  }

  return {
    execute: function (command) {
      commands.push(action(command))
      current = command.execute(current, command.value)
    },

    undo: function () {
      const command = commands.pop()
      current = command.undo(current, command.value)
      console.log("Undo " + action(command) + ": " + command.value)
    },

    getCurrentValue: function () {
      return current
    },

    setCurrentValue: function (value) {
      return (current = value)
    },

    getCommands: () => commands,
  }
}

const calculator = new Calculator()

function executeCommand(expr, value, obj) {
  switch (expr) {
    case "+":
      obj.execute(new AddCommand(value))
      break
    case "-":
      obj.execute(new SubCommand(value))
      break
    case "*":
      obj.execute(new MulCommand(value))
      break
    case "/":
      obj.execute(new DivCommand(value))
      break
    case "%":
      obj.execute(new ModCommand(value))
      break
    default:
      break
  }
}

function calctulateExpression(expression) {
  const obj = new Calculator()

  const expressionArray = []
  const numberStack = []
  const operationStack = []

  let arr = ""

  const operations = {
    "-": 1,
    "+": 1,
    "*": 2,
    "/": 2,
    "%": 2,
  }

  for (let i = 0; i < expression.length; i++) {
    if (expression.match(/\)/g)?.length !== expression.match(/\(/g)?.length) {
      throw new Error("Error:Expression must consists paired brackets")
    }
    if (isNaN(expression[expression.length - 1]) && expression[expression.length] === 0)
      throw new Error("Error: missed number")
    if (arr.length === 0) {
      if ((expression[i].match(/[+-]/) || []).length > 0 || expression[i] === ".") {
        arr += expression[i]
      }else if ((expression[i].match(/[/*%]/) || []).length > 0 || expression[i] === ".") {
        expressionArray.push(expression[i])
      } else if ((expression[i].match(/[1-9]/) || []).length > 0) {
        arr += expression[i]
      }else if ((expression[i].match(/[)]|[(]/) || []).length) {
        expressionArray.push(expression[i])
      }
    } else {
      if ((expression[i].match(/[0-9]/) || []).length > 0) {
        arr += expression[i]
      }else if (expression[i] === "." && !arr.includes(".")) {
        arr += expression[i]
      }else if ((expression[i].match(/[/*%+-]/) || []).length > 0) {
        expressionArray.push(arr)
        expressionArray.push(expression[i]);
        arr = ""
      }else if ((expression[i].match(/[)]|[(]/) || []).length) {
        expressionArray.push(arr)
        arr=""
        expressionArray.push(expression[i])
      }
    }
  }
  arr && expressionArray.push(arr);
  arr=""
  expression = [...expressionArray]

  for (let i = 0; i < expression.length; i++) {
    if (expression[i] >= 0 || expression[i] <= 0) {
      numberStack.push(expression[i])
    } else if ((expression[i].match(/[(/*%+-]/g) || []).length) {
      if (expression[i] === "(") {
        operationStack.push("(")
        continue
      }
      if (operations[expression[i]] <= operations[operationStack[operationStack.length - 1]]) {
        while (
          operations[operationStack[operationStack.length - 1]] >=
            operations[operationStack[operationStack.length - 2]] ||
          operations[expression[i]] <= operations[operationStack[operationStack.length - 1]]
        ) {
          calculate(obj, numberStack, operationStack, executeCommand)
        }
        operationStack.push(expression[i])
      } else {
        operationStack.push(expression[i])
      }
    } else if (expression[i] === ")") {
      while (operationStack[operationStack.length - 1] !== "(") {
        calculate(obj, numberStack, operationStack, executeCommand)
      }
      operationStack.pop()
    }
  }
  while (
    operations[operationStack[operationStack.length - 1]] >=
      operations[operationStack[operationStack.length - 2]] ||
    operationStack.length === 1
  ) {
    calculate(obj, numberStack, operationStack, executeCommand)
  }
  if (isNaN(numberStack[0])) {
    throw new Error("Error: Wrong input")
  }
  return numberStack.join("")
}

export {
  AddCommand,
  SubCommand,
  MulCommand,
  DivCommand,
  ModCommand,
  calculator,
  calctulateExpression,
}
